import React,{useState} from 'react'
import Menu from './Menu';

const Datafile = () => {

    const[state, setState]=useState(Menu);

   const handleBtns=(e)=>{
    
    let word= e.target.value;

    if(word==="All"){
        setState(Menu);
    }
   
else if(word==="new"){
    const filtered= Menu.filter(item => item.orderStage==="new")

    setState(filtered);
}

else if(word==="processing"){
    const filtered= Menu.filter(item => item.orderStage==="processing")

    setState(filtered);
}


else if(word==="finished"){
    const filtered= Menu.filter(item => item.orderStage==="finished")

    setState(filtered);
}
   }

    return (
        <>
            <h1 className="heading">Order your Favourite Food</h1>
            <hr/>
            
        <div className="button-group">
        <button className="btn1" value="All" onClick={handleBtns}>All</button>
        <button className="btn1" value="new" onClick={handleBtns}>new</button>
        <button className="btn1" value="processing"onClick={handleBtns}>processing</button>
        <button className="btn1" value="finished"onClick={handleBtns}>Finished</button>
        </div>

            <div className="box-container">
               
            {
              state.map(item=>{
                  return(
               <div className="card"key={item.Id} >
                       <div className="card-body">
                     <h2>orderStage: {item.orderStage}</h2>
                   <h5> orderName:  {item.orderName}</h5>
                   <p>orderId:{item.orderId}</p>
                    <p> orderDate:  {item.orderDate}</p>
                  
                    
                    </div>
                   </div>
            
                   

                  )
              })
            }
            </div>
        </>
    )
}

export default Datafile;


